body{
    background-color: rgb(188, 193, 201);
}


.heading{
    text-align: center;
    color: white;
    background-color: rgb(245, 106, 106);
    margin-bottom: 50px;
    margin-right: 4%;
    margin-left: 4%;
    font-family: sans-serif;
    font-weight: 500;
    padding: 20px 0;
}


.flex-container{
    display: flex;
    flex-wrap: wrap;
}
.flex-container>div{
    margin-right: auto;
    margin-left: auto;
    background-color: antiquewhite;
    margin-bottom: 50px;
    height: 380px;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
    
}
.flex-container>div:hover{
    box-shadow: 0 0 80px 0 rgb(85, 80, 80);
}
.flex-container>div>img{
    height: 250px;
    width: 338px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.flex-container>div>div>span{
    padding-left: 20px;
    color: grey;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
}
.flex-container>div>div>h3{
    padding-left: 20px;
    font-family: 'Play', sans-serif;
}
.flex-container>div>div>a{
    padding-left: 20px;
}
.flex-container>div>div>a>button{
    background-color: white;
    border: 2px solid rgb(129, 129, 129);
    color: rgb(85, 85, 85);
    font-weight: bolder;
    border-radius: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 35px;
    padding-right: 35px;
}
.flex-container>div>div>a>button:hover{
    cursor: pointer;
    box-shadow:  0 0 5px 0 rgb(85, 88, 95);
}